.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}
/*styling navbar*/
.nav-list{
   display: flex; 
}

.nav-list li{
    padding: 0 1rem;
}

.nav-list li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.nav-list li a:hover {
    background: rgb(228, 196, 228);
    color: black;
    transition: 0.3s;
  }

.hamburger {
    display: none;
}

/*adding media query*/
@media screen and (max-width: 1040px) {
   .nav-list{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: black;
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.5s;
   } 

   .nav-list.active {
      left: 0;
   }
   .nav-list li {
     padding: 1rem 0;
   }

   .nav-list li a {
    font-size: 2rem;
   }


   

   .hamburger {
    display: initial;
}
}