.work-container {
    background: rgb(0,0,2);
}
.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card {
    background: rgb(0,0,2);
    padding: 1.2rem 1rem;
}

.project-card img {
    width: 100%;
}

.project-title {
    color: white;
    padding: 1rem 0;
}

.project-details p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
    font-size: 100%;
}

.pro-btns {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}

.pro-btns .btn {
    background-color: rgb(209,50,19);
    color: whitesmoke;
}

.pro-btns .btn:hover {
    background: rgb(228, 196, 228);
    color: black;
    transition: 0.3s;
  }


@media screen  and (max-width: 740px) {
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}
    
