@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}

body {
  background: black;
}

h1, h4, p,
a {
  color: white;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(233, 125, 233);
  color: black;
  border: 1px solid white;
  font-weight: 600;
  cursor: pointer;
}

.btn-white {
  background: transparent;
  color: white;
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  border: 1px solid white;
  font-weight: 600;
  cursor: pointer;
}

.btn:hover {
  background: grey;
  color: white;
  transition: 0.3s;
}

.btn-white:hover {
  background: grey;
  color: white;
  transition: 0.3s;
}







